    }
  }
  &__img {
    position: relative;
    background: url(../../../../assets/images/work-stages-background.png)
      no-repeat center;
    width: 500px;
    height: 740px;
    margin-left: 15px;

    &::after,
    &::before {
      content: "";
      position: absolute;
      display: block;
      background: #0e4194;
      width: 110px;
      height: 40px;
      left: 86px;
    }

    &::before {
      top: -32px;
    }

    &::after {
      bottom: -39px;
    }
  }
}

@media screen and (max-width: 1400px) {
  .workStages {
    &__img {
    }
  }
}
@media screen and (max-width: 1024px) {
  .workStages__wrap {
    display: none;
  }
}
