.footer {
  border-top: 8px solid #2998ff;
  background: #fbfbfb;
}

.footer__wrap {
  max-width: 1450px;
  padding: 0 10px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 20px;
}

@media screen and (max-width: 1024px) {
  .footer {
    &__wrap {
      ul {
        display: flex;
        li {
          margin-bottom: 0;
          a {
            font-style: normal;
            font-weight: 400;
            font-size: 15px;
            line-height: 27px;
            color: #3f3f3f;
          }
          &:not(:first-child) {
            margin-left: 15px;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 960px) {
  .footer {
    padding-bottom: 15px;
    &__wrap {
      flex-direction: column;
      > div {
        margin-top: 20px;
      }
      ul {
        flex-direction: column;
        order: -1;
        li {
          margin-bottom: 30px;
          &:not(:first-child) {
            margin-left: 0;
          }
        }
      }
    }
  }
}
