.serviceStation {
  background: url(../../../../assets/images/service-station.png) no-repeat
    center;
  border-radius: 30px;
  width: 100%;
  height: 620px;
  padding: 145px 0 165px;
  &__text {
    padding-left: 55px;
    max-width: 685px;
    h1 {
      color: #fff;
      font-weight: bold;
      font-size: 62px;
    }
    > span {
      display: block;
      font-size: 24px;
      color: #fff;
      margin: 20px 0 45px;
    }
  }
}

.accordionMap__text {
  display: flex;
  align-items: center;
  > div {
    display: flex;
    align-items: center;
    margin-left: 60px;
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 29px;
    svg {
      margin: 0 10px;
    }
  }
}

.selectionButton {
  display: flex;
  align-items: center;
  > button {
    padding: 15px;
  }
}

@media screen and(max-width: 768px) {
  .serviceStation {
    &__text {
      h1 {
        font-size: 32px;
        line-height: 35px;
      }
      > span {
        font-size: 16px;
      }
    }
  }
}
