.leftBar {
  display: flex;
  align-items: center;
}

.logoWrap {
  display: flex;
}

.leftBarLocation {
  display: flex;
  align-items: center;
  .location {
    display: flex;
    align-items: center;
    margin-left: 70px;
    font-size: 18px;
    span {
      font-weight: 700;
    }
  }
}

@media screen and (max-width: 1024px) {
  .leftBarLocation {
    width: 50%;
    justify-content: space-between;
    .location {
      margin-left: 0;
    }
  }
}

@media screen and (max-width: 768px) {
  .leftBarLocation {
    .location {
      display: none;
    }
  }
}
